import mzx.ventas.components.layouts.MarkdownLayout as _mzx_ventas_components_layouts_MarkdownLayout
import mzx.ventas.components.layouts.PageLayout as _mzx_ventas_components_layouts_PageLayout
import mzx.ventas.pages.AboutPage as _mzx_ventas_pages_AboutPage
import mzx.ventas.pages.HomePage as _mzx_ventas_pages_HomePage
import mzx.ventas.pages.ProductsPage as _mzx_ventas_pages_ProductsPage

private val LayoutScopeLocal: ProvidableCompositionLocal<Any?> = compositionLocalOf<Any?> { null }

@Composable
private fun <T : Any> currentLayoutScope(): T {
    @Suppress("UNCHECKED_CAST")
    return LayoutScopeLocal.current as? T
        ?: error("Unexpected between Page/Layout scope and the parent Layout which it is providing it. Please report this issue to the Kobweb developers.")
}

@Composable
private fun <T : Any> provideLayoutScope(layoutScope: T, content: @Composable () -> Unit) {
    androidx.compose.runtime.CompositionLocalProvider(LayoutScopeLocal provides layoutScope, content = content)
}

private fun forceReloadNow() {
    window.stop()
    window.location.reload()
}

private fun handleServerStatusEvents() {
    val status = document.getElementById("status")!!
    var lastVersion: Int? = null
    var shouldReload = false

    val warningIcon = status.children[0]!!
    val spinnerIcon = status.children[1]!!
    val statusText = status.children[2]!!

    status.addEventListener("transitionend", {
        if (status.hasClass("fade-out")) {
            status.removeClass("fade-out")
            if (shouldReload) {
                forceReloadNow()
            }
        }
    })

    val eventSource = EventSource("/api/kobweb-status", EventSourceInit(true))
    eventSource.addEventListener("version", { evt ->
        val version = (evt as MessageEvent).data.toString().toInt()
        if (lastVersion == null) {
            lastVersion = version
        }
        if (lastVersion != version) {
            lastVersion = version
            if (document.asDynamic().hidden) {
                // Reload immediately when the page is not visible as the animation will not run
                forceReloadNow()
            } else if (status.className.isNotEmpty()) {
                shouldReload = true
            } else {
                // Not sure if we can get here but if we can't rely on the status transition
                // to reload we should do it ourselves.
                forceReloadNow()
            }
        }
    })

    eventSource.addEventListener("status", { evt ->
        val values: dynamic = JSON.parse<Any>((evt as MessageEvent).data.toString())
        val text = values.text as String
        val isError = (values.isError as String).toBoolean()
        if (text.isNotBlank()) {
            warningIcon.className = if (isError) "visible" else "hidden"
            spinnerIcon.className = if (isError) "hidden" else "visible"
            statusText.innerHTML = "<i>$text</i>"
            status.className = "fade-in"
        } else {
            if (status.className == "fade-in") {
                status.className = "fade-out"
            }
        }
    })

    eventSource.onerror = { eventSource.close() }
}

public fun main() {
    handleServerStatusEvents()

    window.api.logOnError = true

    AppGlobals.initialize(mapOf("title" to "Ventas"))
    BasePath.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.registerLayout(
            "mzx.ventas.components.layouts.MarkdownLayout",
            parentLayoutId = "mzx.ventas.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                mzx.ventas.components.layouts.initMarkdownLayout(ctx)
            },
        ) { pageCtx, pageMethod -> 
            _mzx_ventas_components_layouts_MarkdownLayout {
                pageMethod(pageCtx)
            }
        }
        ctx.router.registerLayout(
            "mzx.ventas.components.layouts.PageLayout",
        ) { pageCtx, pageMethod -> 
            _mzx_ventas_components_layouts_PageLayout(pageCtx) {
                provideLayoutScope(this) {
                    pageMethod(pageCtx)
                }
            }
        }
        ctx.router.register(
            "/",
            layoutId = "mzx.ventas.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                mzx.ventas.pages.initHomePage(ctx)
            },
        ) { pageCtx -> 
            _mzx_ventas_pages_HomePage()
        }
        ctx.router.register(
            "/about",
            layoutId = "mzx.ventas.components.layouts.MarkdownLayout",
            initRouteMethod = { ctx ->
                mzx.ventas.pages.initAboutPage(ctx)
            },
        ) { pageCtx -> 
            _mzx_ventas_pages_AboutPage()
        }
        ctx.router.register(
            "/products",
            layoutId = "mzx.ventas.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                mzx.ventas.pages.initProductsPage(ctx)
            },
        ) { pageCtx -> 
            _mzx_ventas_pages_ProductsPage(pageCtx)
        }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerStyle("headline-text", mzx.ventas.HeadlineTextStyle)
        ctx.theme.registerStyle("subheadline-text", mzx.ventas.SubheadlineTextStyle)
        ctx.theme.registerStyle("footer", mzx.ventas.components.sections.FooterStyle)
        ctx.theme.registerStyle("nav-header", mzx.ventas.components.sections.NavHeaderStyle)
        ctx.theme.registerStyle("markdown", mzx.ventas.components.layouts.MarkdownStyle)
        ctx.theme.registerStyle("page-content", mzx.ventas.components.layouts.PageContentStyle)
        ctx.theme.registerStyle("products-grid", mzx.ventas.pages.ProductsGridStyle)
        ctx.theme.registerStyle("product-card", mzx.ventas.pages.ProductCardStyle)
        ctx.theme.registerStyle("hero-container", mzx.ventas.pages.HeroContainerStyle)
        ctx.theme.registerStyle("home-grid", mzx.ventas.pages.HomeGridStyle)
        ctx.theme.registerStyle("home-grid-cell", mzx.ventas.pages.HomeGridCellStyle)
        ctx.theme.registerVariant("-circle", mzx.ventas.CircleButtonVariant
        )
        ctx.theme.registerVariant("-uncolored", mzx.ventas.UncoloredButtonVariant
        )
        ctx.theme.registerKeyframes("side-menu-slide-in", mzx.ventas.components.sections.SideMenuSlideInAnim)
        mzx.ventas.initSiteStyles(ctx)
        mzx.ventas.initColorMode(ctx)
        mzx.ventas.initStyles(ctx)
        mzx.ventas.initTheme(ctx)
    }

    // Dedup any leading slashes after removing the origin, just in case someone typed
    // something like `https://site.com//about` by accident. If we pass `//about` into
    // `tryRoutingTo`, Kobweb will reject it as a protocol-relative URL; instead, we
    // want it to navigate to `/about`
    router.tryRoutingTo(BasePath.remove("/" + window.location.href.removePrefix(window.origin).trimStart('/')), UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("_kobweb-root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "_kobweb-root") {
        mzx.ventas.AppEntry {
            router.renderActivePage { DeferringHost { it() } }
        }
    }
}
