package mzx.ventas.data

import kotlinx.browser.window
import kotlinx.coroutines.await
import kotlinx.serialization.json.Json
import mzx.ventas.pages.ProductItem

suspend fun loadJson(): List<ProductItem> {
    val response = window.fetch("/data/data.json").await()
    val text = response.text().await()
    val obj = Json.decodeFromString<List<ProductItem>>(text)

    console.log(obj)

    return obj
}
